import { mapState } from "vuex";
export default {
  name: "ArticleCommenCard",
  props: {
    comment: {
      type: Object,
      default: () => {
        return {};
      },
    },
  },
  computed: {
    ...mapState(["user"]),
  },
  methods: {
    delComment() {
      this.$emit('delComment', this.comment)
    }
  }
};
